.banner {
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner_contents {
    margin-left:30px;
    padding-top:140px;
    height: 190px;
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .3rem;
}

.banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: .8rem;
    max-width: 360px;
    height: 80px;
}

.banner_button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: .5rem;
    background: rgba(51, 51, 51, .5);
    padding-bottom: .5rem;
}

.banner_button:hover{
    color: #000;
    background: #e6e6e6;
    transition: all .2s;
}

.banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37, .61),
        #111);
}